import { useHistory } from "react-router-dom";
import React, { useState, useEffect, useContext } from "react";
import Axios from "axios";
import UserContext from "../../context/UserContext";
//import { useSelector } from "react-redux";
import Moment from "react-moment";
import 'moment/locale/es-us';
import DeleteIcon from '@mui/icons-material/Delete';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import IconButton from '@mui/material/IconButton';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import MoreVertIcon from '@mui/icons-material/MoreVert'
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';

import { useToasts } from "react-toast-notifications";
import * as Actions from "../../redux/action";
import "./index.css";
import logoImg from "../../images/fino.png";

import { useDispatch } from "react-redux";
import {
  SwipeableList,
  SwipeableListItem,
  SwipeAction,
  TrailingActions,
  Type as ListType
} from "react-swipeable-list";
import "react-swipeable-list/dist/styles.css";



const Transition = React.forwardRef(function Transition(props, ref) {
      return <Slide direction="up" ref={ref} {...props} />;
    });

const Item = styled(Paper)(({ theme }) => ({
      position:'relative',
  backgroundColor:'#fcfeffd6',
  padding: theme.spacing(1),
  textAlign: 'left',
  color: theme.palette.text.secondary,
}));


const Img = styled('img')({
      display:'block',
      width:'30px',
      heigth:'30px'
    });

export default function Translist() {

  const dispatch = useDispatch();
    const history = useHistory();
    const [tranData, setTranData] = useState([]);
    const { loginStatus, setLoginStatus, setConf, setPdf } = useContext(UserContext);
    const [sellerId, setSellerId] = useState([]);
 //   const email = useSelector((state) => state.email.currentEmail);
    const [totalAmount, setTotalAmount] = useState(0);
    const [openDialog, setDialogOpen] = React.useState(false);
    const [openDialog2, setDialog2Open] = React.useState(false);
    const [directSale, setDirectSale] = React.useState(false)
    const { addToast } = useToasts();
    useEffect(() => {
      const checkLoggedIn = async () => {
            let token = localStorage.getItem("auth-token");
            if (token === null) {
              // history.push("/");
              // return;
            }else{
              const userRes = await Axios.get("/users/", {
                headers: { "x-auth-token": token },
              });
              setLoginStatus({
                token,
                email: userRes.data.email,
                customerId:''
              });
              Axios.get("/users/getId/" + userRes.data.email).then(async (response) => {
                  setSellerId(response.data);
                  setDirectSale(response.data.directSale);
                  if(!response.data.name){
                    setDialogOpen(true)
                  }
                });
                  Axios.get("/trans/all/" + userRes.data.id).then(
                    async (response) => {
                        const currentDate = new Date();
                        const curFormatDate = currentDate.toLocaleDateString('en-US', {
                          hour12: false,
                        });
            
                        var totalAmount = 0;
                        for (var i = 0; i < response.data.length; i++) {
                            const tranFormatDate = new Date(response.data[i].date)
                                .toLocaleDateString('en-US', {
                                  hour12: false,
                                })
                            if (curFormatDate === tranFormatDate && response.data[i].cancelled !== true) {
                                totalAmount += response.data[i].amount;
                            }
                        }
                        setTotalAmount(totalAmount);
                        setTranData(response.data);
                    }
                );
              }
      };
      checkLoggedIn();
      return(()=>{
        setTotalAmount(0);
        setTranData([]);
        setSellerId([])
      })
    // eslint-disable-next-line react-hooks/exhaustive-deps
}, []);
   




//Dialog Settings --------------------
    const handleDialogClickOpen = () => {
      handleClose()
      if(sellerId["name"]){
        setDialog2Open(true)
      } else{
      setDialogOpen(true);
      }
    };
    const handleDialogClose = () => {
      setDialog2Open(false)
      setDialogOpen(false);
    };
// Dropmenu settings ----------------------------
      const [anchorEl, setAnchorEl] = React.useState(null);
      const open = Boolean(anchorEl);
      const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
      };
      const handleClose = () => {
        setAnchorEl(null);
      };
//----------------------------------------------------
      const goCustomer = () => {
        localStorage.removeItem('customerId')
        if(directSale === true){
          
          history.push("/invite");
        }else{
          history.push("/customer");
        }
      };
         
      const logout = () => {
        setLoginStatus({
          token: undefined,
          email: undefined,
          customerId:''
        });
        localStorage.removeItem("auth-token");
        localStorage.removeItem("customerId");
        history.push("/");
        return;
      };
// Show transaction all ----------------------
        const [showAll , setShowAll] = useState(false);
        const setting = ()=>{
            setShowAll(prev =>{
                return !prev
            })
        }
        const length = tranData.length;

        const click = (amount, id, pdfLink, name, date, cancelled)=>{
          if(cancelled === true){

          }else{
            setConf(false);
            setPdf(pdfLink);
              setLoginStatus({
                ...loginStatus,
                customerId: id
              })
              if(id){
                  localStorage.setItem("customerId", id);
              }
              let sel ={
                name: name,
                date: date
            }
            dispatch(Actions.selData(sel));
              history.push({
                pathname: "/confirm/" + amount,
            });
          }
        }
        const cancelApi =async (row, index) => {
          const url = row.pdfLink[0];
          const parse = url.split('/');
          let data =  {
            "contribuyente":{
               "token_contribuyente":sellerId.token,
                "id_usuario_vendedor":parseInt(sellerId.vendor),
               "tipo_proceso":sellerId.process
            },
            "cabecera_comprobante":{
               "tipo_documento":parse[7].toString(),
               "serie_comprobante":parse[8],
               "numero_comprobante":parseInt(parse[9]),
               "motivo_anulacion":"Error en documento" 
            }
        }
        const postDetail = {
          method: 'POST',
          headers: {
             "Content-Type": "application/json",
          },
          body: JSON.stringify(data)
       }

      let apiUrl = 'https://facturavirtual.pe/facturacion/api/comunicacion_baja';
    try{
      await fetch(apiUrl, postDetail).then( async (res)=>{
        return res.json()
      }).then(async (response)=>{
        if(response.respuesta === "ok"){
          await Axios.put('/trans/cancel/', {id : row.id}).then(async (response)=>{
                  let temp1 = [...tranData];
                  let temp2 = {...temp1[index]}
                  temp2.cancelled = true;
                  temp1[index]= temp2;
                  setTranData(temp1)
                  const cur = new Date().toLocaleDateString('en-US', {
                    hour12: false,
                  });
                  const tran = new Date(response.data.createdAt)
                      .toLocaleDateString('en-US', {
                        hour12: false,
                      })
                  if (cur === tran) {
                    setTotalAmount(prev=> prev-row.amount)
                  }
              })
          }else{
            addToast(response.mensaje, {
              appearance: "warning",
              autoDismiss: true,
          });
  
          }
      })
    }catch(err){
      addToast("id_usuario_proveedor no es válido, vincule al administrador", {
        appearance: "warning",
        autoDismiss: true,
    });
    }
    
       
        }
        const gotoProfile = ()=>{
          history.push('/profile')
        }
        const trailingActions = (row, index) => (
          row.cancelled !== true ? <TrailingActions sx={{overflow:'hidden'}}>
            <SwipeAction destructive={false} onClick={()=>cancelApi(row, index)}>
              <Button variant="text"  startIcon={<DeleteIcon size="large" />}>
              </Button>
            </SwipeAction>
          </TrailingActions> : <></>
        );
  return (
      <Grid
      sx={{
            height: '100vh',
            color:'white'
          }}
      container
      direction="column"
      justifyContent="flex-start"
    >
      <Grid item className="header" container spacing={2} dirction="row" justifyContent="space-between" sx={{backgroundColor:"#1a1938", p:2}}>
        <Grid item xs={2}
  justifyContent="space-between" container>
            <Img src={logoImg} alt="logo" sx={{my:'auto'}}/>
        </Grid>
        <Grid item xs={8}>
            <Grid>
            {sellerId["name"] ? sellerId["name"] : <Button variant="filled" onClick={handleDialogClickOpen}>
            BIENVENIDO A FINO
                  </Button>}
                  
            </Grid>
            <Grid>
              {sellerId["ruc"] ? `RUC: ${sellerId["ruc"]}` : "VERSION PRUEBA"}
            </Grid>
        </Grid>
        <Grid item xs={2} justifyContent="flex-end" container>
            <IconButton
                  id="basic-button"
                  aria-controls={open ? 'basic-menu' : undefined}
                  aria-haspopup="true"
                  aria-expanded={open ? 'true' : undefined}
                  onClick={handleClick}
                  sx={{color:'white'}}
            >
                  <MoreVertIcon sx={{color:'white'}}/>
            </IconButton>
            <Menu
                  id="basic-menu"
                  anchorEl={anchorEl}
                  open={open}
                  onClose={handleClose}
                  MenuListProps={{
                  'aria-labelledby': 'basic-button',
                  }}
            >
                  <MenuItem onClick={gotoProfile}>Perfil</MenuItem>
                
                  <MenuItem onClick={handleDialogClickOpen}>Soporte</MenuItem>

                  <MenuItem onClick={logout}>Salir</MenuItem>
            </Menu>          
        </Grid>
        <Grid item xs={12}>
            <Item >
                  <span>Ventas Hoy</span>
                  <span style={{position:'absolute',right:'10px'}}>S/&nbsp;<span>{totalAmount.toFixed(2)}</span></span>
            </Item >
           
        </Grid>
      </Grid>
      <Grid item className="body" >
      <Paper sx={{ overflow: 'hidden', width:'100%' }}>
      <TableContainer sx={{ maxHeight: 'calc(100vh - 220px)' }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
          <TableRow>
            <TableCell >
              <div style={{display:'flex', justifyContent:'space-between'}}>
                <span>Últimos Movimientos</span>
                <Button  variant="text"
                      color="info" onClick={setting} sx={{fontSize:'13px', p:0, ml:10}}>Ver Todos</Button>
              </div>
              
            </TableCell>
          </TableRow>
          </TableHead>
          <TableBody>
          <SwipeableList sx={{p:0, m:0}} type={ListType.IOS}>
          {tranData.map((row, index) => (
             <SwipeableListItem
             key={index}
             blockSwipe={row.cancelled}
             //leadingActions={leadingActions()}
             trailingActions={trailingActions(row, index)}
           >

              <TableCell onClick={()=>click(row.amount, row.customerId, row.pdfLink, row.name, row.date, row.cancelled)} scope="row" sx={{p:1,pl:1,mb:1, width:'100%', display:'flex', justifyContent:'space-between'}}>
                <div>
                    <strong>{row["name"] === "Invited" ? "Invitado" : row["name"]}</strong>
                  <p className="type">
                    {row["type"] === 'invoice'? "Factura" : (row["type"] === "ticket" ? "Boleta" : "Nota")}&nbsp;::&nbsp;
                    <Moment locale="es-us" format="DD MMMM  - HH:mm" >
                      {row["date"]}
                    </Moment>
                  </p>
                </div>
                {row["cancelled"] !==true ? <div style={{paddingTop:'12px'}}>S/&nbsp;{row.amount}&nbsp;</div> :
                <div style={{paddingTop:'12px', color:'#ff5200'}}>Cancelled</div>}
              </TableCell>
              {/* <TableCell align="right" >S/&nbsp;{row.amount}&nbsp;</TableCell> */}

            </SwipeableListItem>
          )).reverse().slice(0, (showAll === true ? length : 8)) 
          }</SwipeableList>
          
          </TableBody>
          
        </Table>
      </TableContainer>
      
    </Paper>
      </Grid>
      <Grid item className="footer" sx={{position:'fixed', bottom:'0px',width:'100%'}}>
                  <Button
                    variant="contained"
                    onClick={goCustomer}
                    color="info"
                    sx={{marginBottom:'10px', width:'60%'}}
                    >
                    ENVIAR
                </Button>
      </Grid>

      <Dialog
        open={sellerId["name"]? false : openDialog}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleDialogClose}
        fullWidth
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle sx={{mx:'auto'}} color="primary" >{"Adquiere la versión Full !!"}</DialogTitle>
        <DialogContent>
          <DialogContentText sx={{fontSize:'80%'}} id="alert-dialog-slide-description">
          Bienvenido a Fino. Actualmente estas en version Free. Para utilizar la versión PRO. por favor contactenos al: 943204360 o haciendo click en el boton de abajo.
          </DialogContentText>
        </DialogContent>
        <DialogActions sx={{mx:'auto'}}>
          <Button sx={{p:1, fontSize:'70%', }} startIcon={<WhatsAppIcon color="white" sx={{fontSize:'80px'}} fontSize="large" />} color="success" variant="contained" onClick={()=>{window.open("https://api.whatsapp.com/send?phone=+51943204360&text=Hola,%20deseo%20soporte%20para%20integrar%20mi%20sistema%20a%20FINO.");
      }}>Contactar por WhatsApp</Button>
        </DialogActions>
      </Dialog>
      <Dialog
        open={openDialog2}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleDialogClose}
        fullWidth
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle sx={{mx:'auto'}} color="primary" >{"Estás en la versión Pro"}</DialogTitle>
        <DialogActions sx={{mx:'auto'}}>
          <Button sx={{p:1, fontSize:'70%', }} startIcon={<WhatsAppIcon color="white" sx={{fontSize:'80px'}} fontSize="large" />} color="success" variant="contained" onClick={()=>{window.open("https://api.whatsapp.com/send?phone=+51943204360&text=Hola,%20deseo%20soporte%20para%20integrar%20mi%20sistema%20a%20FINO.");
      }}>Contactar por WhatsApp</Button>
        </DialogActions>
      </Dialog>
    </Grid>
  );
}